import React, { Component } from "react";
import { Route } from "react-router-dom";

// Components
import Developer from "./authors/Developer";
import Redactor from "./authors/Redactor";
import Writer from "./authors/Writer";
import { Default } from "./authors/Default";

class Marker {
  public mark = (symbol: any, mark: boolean = false): JSX.Element => {
    if (mark) {
      return (
        <span className="tp-upper-case" key={1 + Math.floor(Math.random() * 10000)}>
          {symbol}
        </span>
      );
    } else {
      return <span key={1 + Math.floor(Math.random() * 10000)}>{symbol}</span>;
    }
  }
}

export class AuthorsPage extends Component {
  private name: (string | JSX.Element)[];
  public marker: Marker;

  public constructor(props: object) {
    super(props);

    this.name = [];
    this.marker = new Marker();
  }

  public upperCaseColorizer = (item: (string | JSX.Element)[]): (string | JSX.Element)[] => {
    if (typeof item === "string") {
      let strItem: string = String(item);
      let token: RegExpMatchArray | null = strItem.match(/[A-Z-]/);

      if (token !== null && token !== undefined) {
        let tokenIndex: number = token!.index || 0;
        let result: (string | JSX.Element)[] = [
          this.marker.mark(strItem.substring(0, token.index)),
          this.marker.mark(token[0], true),
          strItem.substring(tokenIndex + 1)
        ];

        this.upperCaseColorizer(result);
      }
    } else {
      let string: string = String(item).slice(-1);
      let token: RegExpMatchArray | null = string.match(/[A-Z-]/);

      if (token != null) {
        let tokenIndex: number = token!.index || 0;
        let result: (string | JSX.Element)[] = [
          ...item,
          this.marker.mark(string.substring(0, token.index)),
          this.marker.mark(token[0], true),
          string.substring(tokenIndex + 1),
        ];

        this.upperCaseColorizer(result);
      } else {
        this.name = [...item, this.marker.mark(string)];
      }
    }

    return this.name;
  };

  colorizeNameCapsLetters = (name: (string | JSX.Element)[]) => {
    return this.upperCaseColorizer(name);
  };

  render = (): JSX.Element => {
    return (
      <React.StrictMode>
        <Route
          exact
          path={`/${this.props["location"].authors}/id=1`}
          component={Developer}
          render={() => (
            <Developer colorizeName={this.colorizeNameCapsLetters} />
          )}
        />
        <Route
          exact
          path={`/${this.props["location"].authors}/id=2`}
          component={Redactor}
          render={() => (
            <Redactor colorizeName={this.colorizeNameCapsLetters} />
          )}
        />
        <Route
          exact
          path={`/${this.props["location"].authors}/id=3`}
          component={Writer}
          render={() => (
            <Writer colorizeName={this.colorizeNameCapsLetters} />
          )}
        />
        <Route
          exact
          path={`/${this.props["location"].authors}`}
          render={(props: any) => (
            <Default
              {...{
                ...props,
                colorizeNameCapsLetters: this.colorizeNameCapsLetters,
              }}
            />
          )}
        />
      </React.StrictMode>
    );
  }
}
